import React from 'react';
import Header from './Componente/Header';
import './style/App.css';
import {useEffect} from 'react';


function Sobre() {
  useEffect(()=>{
    document.title='Gilberto';
  });
  return (
    <div className="App">
      <Header/>
        Sobre
    </div>
  );
}

export default Sobre;